<template>
  <main class="app-main">
    <section class="top">
      <p class="title">
        <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>需求对接信息
      </p>
    </section>
    <div class="con-con">
      <div class="list">
        <span class="img">*</span>
        <md-input-item v-model="userInfo.name" title="对接人" ref="input11" placeholder="请输入对接人姓名"></md-input-item>
      </div>
      <div class="list">
        <span class="img">*</span>
        <md-input-item
          title="对接人类型"
          ref="input11"
          :clearable="true"
          :placeholder="userType1==1 ? '个人':'单位'"
          readonly
          class="read"
        ></md-input-item>
      </div>

      <div class="list">
        <span class="img">*</span>
        <md-input-item
          title="所在单位"
          ref="input11"
          v-model="userInfo.unit"
          :clearable="true"
          placeholder="请输入所在单位"
        ></md-input-item>
      </div>
      <div class="list">
        <span class="img">*</span>
        <md-input-item
          title="联系邮箱"
          ref="input11"
          v-model="userInfo.email"
          :clearable="true"
          placeholder="请输入联系邮箱"
        ></md-input-item>
      </div>
      <div class="list">
        <span class="img">*</span>
        <md-input-item
          title="联系电话"
          ref="input11"
          v-model="userInfo.phone"
          :clearable="true"
          placeholder="请输入联系电话"
        ></md-input-item>
      </div>
      <div class="list">
        <span class="img">*</span>
        <md-input-item
          title="验证码"
          ref="input11"
          v-model="code"
          :clearable="true"
          placeholder="请输入验证码"
        >
          <div v-if="!isSendShow" class="input-operator" slot="right" @click="sendCode">获取验证码</div>
          <div v-if="isSendShow" class="min" slot="right">{{ num }}秒</div>
        </md-input-item>
      </div>
      <div class="list tarea">
        <span class="img">*</span>

        <md-textarea-item
          ref="demo0"
          title="对接事描述"
          class="example"
          v-model="userInfo.intro"
          :autosize="true"
          :rows="1"
          placeholder="请您对 对接事项作简要的描述"
          :max-height="440"
        />
      </div>
    </div>
    <md-button type="default" @click="onRegister">提交信息</md-button>
    <md-dialog :closable="true" v-model="iconDialog.open" :btns="iconDialog.btns">
      <img src="@/assets/technology/img@2x.png" alt />
      <p>对接信息已发送,</p>
      <p>工作人员将会在24小时内联系您!</p>
    </md-dialog>
  </main>
</template>
<script>
import {
  Field,
  CellItem,
  InputItem,
  Toast,
  Icon,
  Picker,
  Button,
  TextareaItem,
  ActivityIndicator,
  Landscape,
  Dialog
} from "mand-mobile";
// import { userType } from "../../utils/select";
import { docking } from "@/apiUrl/technology";

import { sendCode, register, testCode } from "../../apiUrl/login";

import qs from "qs";

export default {
  name: "index",
  components: {
    [Field.name]: Field,
    [Button.name]: Button,
    [Picker.name]: Picker,
    [InputItem.name]: InputItem,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [TextareaItem.name]: TextareaItem,
    [ActivityIndicator.name]: ActivityIndicator,
    [Landscape.name]: Landscape,
    [Dialog.name]: Dialog
  },
  data() {
    return {
      showMaskClosable: false,
      iconDialog: {
        open: false
      },
      isEducShow: false,
      userType1: this.$store.state.user.userInfo.roleId,
      num: 60,
      isSendShow: false,
      againPassword: "",
      iphone: "",
      code: "",
      userInfo: {
        name: "",
        unit: "",
        intro: "",
        email: "",
        phone: "",
        type: this.$route.query.type
      }
    };
  },
  methods: {
    sendCode() {
      if (/^1[3456789]\d{9}$/.test(this.userInfo.phone)) {
        const args = {
          phone: this.userInfo.phone
        };
        sendCode(args).then(res => {
          if (res.statusMsg === "success") {
            Toast.succeed("短信已发送！");
          }
        });
        this.isSendShow = true;
        const val = setInterval(() => {
          this.num--;
          if (this.num === 0) {
            clearTimeout(val);
            this.isSendShow = false;
            this.num = 60;
          }
        }, 1000);
      } else {
        Toast.failed("手机号格式不正确");
      }
    },
    onRegister() {
      if (!this.userInfo.name) {
        Toast.failed("请输入对接人姓名");
        return;
      }
      if (!this.userInfo.unit) {
        Toast.failed("请输入单位");
        return;
      }
      if (!this.userInfo.email) {
        Toast.failed("请输入邮箱");
        return;
      }
      if (!this.userInfo.phone) {
        Toast.failed("请输入联系电话");
        return;
      }
      if (!this.code) {
        Toast.failed("请输入验证码");
        return;
      }
      let reg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      if (!reg.test(this.userInfo.email)) {
        Toast.failed("邮箱格式有误");
        return;
      }
      if (!this.userInfo.intro) {
        Toast.failed("请输入简述");
        return;
      }
      // 验证码
      let codeData = {
        phone: this.userInfo.phone,
        code: this.code
      };
      testCode(codeData)
        .then(res => {
          docking(this.userInfo)
            .then(res => {
              this.iconDialog.open = true;
            })
            .catch(err => {
              Toast.failed(err);
            });
        })
        .catch(e => {
          Toast.failed(e);
        });
    },

    onEducShow() {
      document.activeElement.blur();
      this.isEducShow = true;
    },
    // 回退
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="stylus" scoped>
/deep/.md-field-item-title
  text-align: left
  padding-left: 4px
  width: 180px !important
.read
  background: rgba(153, 153, 153, 0.1)
/deep/.tarea
  overflow: hidden
  textarea
    width: 92%
    height: 240px !important
    border: 1px solid rgba(153, 153, 153, 0.3)
    border-radius: 6px
    padding: 21px 0 10px 15px
    margin-right: 30px
/deep/.intro
  input
    width: 480px
    height: 240px
    border: 1px solid rgba(153, 153, 153, 0.3)
    border-radius: 6px
.input-operator
  width: 150px
  height: 50px
  color: #fff
  font-size: 18px !important
  line-height: 50px
  margin-right: 20px
  background: linear-gradient(0deg, rgba(202, 2, 57, 1), rgba(255, 97, 53, 1))
  border-radius: 4px
.min
  width: 150px
  height: 50px
  color: #999
  background: #E5E8EE
  font-size: 18px !important
  line-height: 50px
  margin-right: 20px
.con-con
  width: 100%
  margin-top: 150px
  .list
    img
      width: 30px
      height: 30px
      float: left
      margin-left: 32px
      margin-top: 30px
    .img
      width: 13px
      height: 13px
      font-size: 28px
      float: left
      margin-left: 29px
      margin-top: 35px
      color: rgba(212, 31, 31, 1)
/deep/.md-cell-item-body
  justify-content: space-between !important
  padding: 0px 20px
  text-align: left
/deep/.md-cell-item-content
  flex: 1 1 0% !important
/deep/.md-cell-item-left
  margin-right: 10px
/deep/.md-cell-item
  height: 91px
/deep/.md-cell-item-body:before
  top: 91px
  bottom: auto
/deep/.md-button
  width: 660px
  height: 88px
  background: rgba(2, 25, 85, 1)
  border-radius: 12px
  margin: 0 auto
  color: #fff
  margin-top: 60px
</style>
<style lang="stylus" scoped>
/deep/.md-dialog-body
  display: flex
  justify-content: center
  flex-direction: column
  align-items: center
  text-align: center
  img
    width: 80%
  p
    margin: 15px auto
</style>
